import React from "react"
import Layout from "../components/Layout/Layout.jsx"
import Result from "../components/Result/Result";
import BannerResult from "../components/Banner/BannerResult.jsx";

class searchPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            searchKey: '',
            window
        }

        this.getKey =this.getKey.bind(this)
        this.updateSearchKey = this.updateSearchKey.bind(this)

        this.updateSearchKey()
    }

    // 统一处理url的变化，更新props, 监听不到的
    // componentDidMount() {
        // window.addEventListener('pushstate', this.updateSearchKey)
    // }

    // componentWillUnmount() {
        // window.removeEventListener('pushstate', this.updateSearchKey);
    // }

    componentDidUpdate() {
        let _currUrl = this.getKey() 
        _currUrl != this.state.searchKey && this.updateSearchKey()
    }

    getKey() {
        return this.state.window.location.search.substring(5) || ''
    }

    updateSearchKey() {
        console.log('update key')
        this.setState({
            searchKey: this.getKey()
        }) 
    }

    render() {
        return (
            <Layout>
                {console.log(this)}
                <BannerResult info={{ searchKey: this.state.searchKey }}></BannerResult>
                <Result searchKey={this.state.searchKey}></Result>
            </Layout>
        )
    }
}

export default searchPage
